@import "variables.module.scss";

.pill {
  font-size: 10px;
  padding: 3px 8px;
  line-height: 10px;
  background: $light_gray_2;
  display: inline-block;
  border-radius: 8px;
  cursor: pointer;
  border: 1px solid darken($light_gray_2, 5%);

  &.clickable:hover {
    background: darken($light_gray_2, 5%);
  }

  .icon {
    font-size: 10px;
    position: relative;
    bottom: -1px;
    margin-left: 5px;
  }
}

.large {
  font-size: 12px;
  line-height: 12px;
  padding: 4px 8px;

  .icon {
    font-size: 10px;
    position: relative;
    bottom: 0px;
  }
}
