@import 'variables.module.scss';

.template_text {
  margin-top: 10px;
  padding: 10px;
  background: $light_gray_2;
  border-radius: 5px;
  margin-bottom: 10px;
}

.variable {
  margin-bottom: 6px;
}

.crossIcon {
  margin-right: 10px;
}

.error {
  display: flex;
  margin-top: 10px;
  color: red;
  font-weight: 500;
}

.url_attachment {
  margin-bottom: 8px;
  display: flex;
  align-items: center;

  .upload {
  }

  .remove_upload {
    margin-left: 8px;
    margin-top: 4px;
    display: inline-block;
  }

  .url {
    display: inline-block;
    margin-left: 8px;
    width: 329px;
    margin-right: 10px;
    input {
      vertical-align: top;
      height: 28px !important;
      margin-top: -11px;
      width: 100%;
      font-size: 12px;
    }
  }

  .remove {
    margin-top: 4px;
  }
}

.type_choice {
  display: inline-block;
  width: 180px;
}
